<template>
	<div class="question">
		<div class="carousel">
			<img :src="carousel">
		</div>

		<Qa :data="questionData" />	
	</div>
</template>

<script>
	import Qa from '@/components/Qa.vue'

	export default {
		components: { Qa },
		data(){
			return {
				questionData: [],
				carousel: `${process.env.BASE_URL}images/about/about_carousel.png`,
			}
		},
		mounted() {
			fnQuestion(this);
			this.$emit('handlerTabbar', false);
		}
	}

	function fnQuestion(that, params) {
		that.$request.question(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.questionData = result;
			}
		})
	}
</script>

<style lang="scss">
	@import './../styles/question.scss';
</style>